@import url(https://fonts.googleapis.com/css?family=Lato:900&display=swap);
* {
    color: black;
}

h1 {
    font-weight: 900;
}

span {
    font-weight: 900;
}

#home-text {
    /* margin-top: 2vw; */
    width: 86%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}

#welcome-to {
    color: black;
    font-size: 6.5vw;
    line-height: 6.5vw;
}

#loyola {
    color: #a40047;
    font-size: 6.5vw;
    line-height: 6.5vw;
}

#university {
    color: #e7af12;
    font-size: 6.5vw;
    line-height: 6.5vw;
}

#chicago-coding {
    line-height: 6.5vw;
}

#chicago {
    color: #a40047;
    font-size: 6.5vw;
    line-height: 6.5vw;
}

#coding {
    color: black;
    font-size: 8.5vw;
    float: right;
    margin-left: 2vw;
}

#community {
    color: black;
    font-size: 8.5vw;
    line-height: 6vw;
    float: right;
}

@media (max-aspect-ratio: 1/1) {
    #home-text {
        width: auto;
        height: auto;
        display: flex;
        justify-content: start;
        overflow: hidden;
    }

    #welcome-to {
        font-size: 10vw;
        line-height: 10vw;
    }

    #loyola {
        font-size: 12vw;
        line-height: 12vw;
    }

    #university {
        font-size: 12vw;
        line-height: 12vw;
    }

    #chicago {
        font-size: 12vw;
        line-height: 12vw;
    }

    #chicago-coding {
        line-height: 10vw;
    }

    #coding {
        float: left;
        font-size: 13vw;
        line-height: 13vw;
        margin-left: 0vw;
    }

    #community {
        float: left;
        font-size: 13vw;
        line-height: 13vw;
    }
}
* {
    color: black;
}

#events-grid {
    width: 100%;
}

#events-title {
    font-size: 7vh;
    font-weight: 900;
    float: right;
}

#list-column {
    padding-top: 1.5vh;
    padding-left: 4vw;
    text-align: right;
    float: right;
    height: 62.5vh;
    overflow-y: scroll;
    font-size: 1.5em;
}

.past {
    text-decoration: line-through;
}

@media (max-aspect-ratio: 1/1) {
    #events-title {
        float: left;
    }

    #mobile-list {
        font-size: 1.25em;
    }
}
* {
    color: black;
}

#exec-grid {
    width: 100%;
}

#exec-title {
    font-size: 7vh;
    font-weight: 900;
}

.board-member {
    display: flex !important;
    justify-content: center !important;
}

.headshot {
    width: 14vw;
    height: 25vh;
    object-fit: cover;
}

.member-card {
    max-width: 14vw !important;
}

.member-name {
    text-align: center;
}

.grid-row {
    width: 100%;
    display: flex;
    justify-content: center;
}

@media (max-aspect-ratio: 1/1) {
    .headshot {
        width: 90%;
        height: auto;
    }

    .member-card {
        max-width: 90% !important;
    }
}
* {
    color: black;
}

#contact-grid {
    width: 100%;
    margin-top: 3vh;
}

#image-column {
    display: flex;
    justify-content: center;
}

#form-column {
    padding-left: 4vw;
}

#contact-title {
    font-size: 7vh;
    font-weight: 900;
    float: right;
    line-height: 4vh;
}

#contact-text {
    margin-top: 1em;
    font-size: 1.5em;
    float: right;
    text-align: right;
    margin-bottom: 1.5em;
}

#cs-award {
    width: 65%;
    height: auto;
}

#submit {
    width: 100%;
    margin-top: 2vh;
}

@media (max-aspect-ratio: 1/1) {
    #contact-title {
        float: left;
        line-height: normal;
    }
    
    #contact-text {
        float: left;
        text-align: left;
        margin-bottom: 1.5em;
    }

    #image-wrapper {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: center;
    }
}

#nav-bar {
    margin-top: 2vh;
    margin-bottom: 2vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#logo {
    height: 12.5vh;
    width: auto;
}

.nav-item {
    color: black;
    font-size: 1.5em;
}

.nav-item:hover {
    color: #555;
}
* {
    color: black;
}

#about-grid {
    width: 100%;
}

#about-div {
}

#about-title {
    font-size: 7vh;
    font-weight: 900;
}

#text-column {
    padding-right: 4vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-text {
    font-size: 1.75em;
}

.about-text-mobile {
    font-size: 1.25em;
}
